.rb-badge {
    border-radius: 2px;
    color: white;
    text-transform: none;
    font-weight: 500;
    font-size: 0.56rem;
    flex-shrink: 0;
}

.rb-badge code,
  .rb-badge .code {
    padding: 0;
    border: 0;
    background: transparent;
}

.rb-badge.tag {
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif !important;
}

.rb-badge-toolbelt {
    background: #2f0049;
}

.rb-badge-ux {
    background: #00205b;
}

.rb-badge-labs {
    position: relative;
    overflow: hidden;
    background: transparent;
}

.rb-badge-labs::before {
    position: absolute;
    top: -75%;
    right: -50%;
    bottom: -75%;
    left: -50%;
    z-index: -1;
    background-image: radial-gradient(circle at top right, #d914cf 0%, rgba(154, 105, 241, 0) 65%);
    content: '';
    opacity: 1;
    animation: before 16s infinite cubic-bezier(0.68, -0.55, 0.265, 1.55);
    animation-direction: alternate;
}

.rb-badge-labs::after {
    position: absolute;
    top: -60%;
    right: -60%;
    bottom: -60%;
    left: -60%;
    z-index: -2;
    background-image: linear-gradient(-225deg, #2f0049 15%, #00205b 50%, #e8edf3 85%);
    content: '';
    opacity: 1;
    transform: translateY(-20%) translateX(-20%) rotateX(10rem);
    animation: after 35s infinite linear;
}

@keyframes before {
    0% {
        opacity: 0;
        transform: rotate(-30deg);
    }

    25% {
        opacity: 1;
        transform: rotate(30deg);
    }

    50% {
        opacity: 0.75;
        transform: rotate(-90deg);
    }

    75% {
        opacity: 0.5;
        transform: rotate(-120deg);
    }

    100% {
        opacity: 0;
        transform: rotate(60deg);
    }
}

@keyframes after {
    0% {
        transform: translateY(-20%) translateX(-20%) rotate(-5deg);
    }

    25% {
        transform: translateY(20%) translateX(20%) rotate(0deg);
    }

    50% {
        transform: translateY(-20%) translateX(20%) rotate(5deg);
    }

    75% {
        transform: translateY(20%) translateX(-20%) rotate(0deg);
    }

    100% {
        transform: translateY(-20%) translateX(-20%) rotate(-5deg);
    }
}

