    border: none;
}

.example-code .copy-code {
    position: absolute;
    top: 1em;
    right: 0.5em;
    border: none;
    opacity: 0;
    transition: opacity 100ms linear;
}

.example-code pre {
    flex: 1;
    margin: 0;
}
